<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('FUNCTION_NAME') }}</label>
          <vs-input class="w-full" v-model="bankFunction.functionName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('ICON_CLASS') }} <a href="https://fontawesome.com/icons?d=gallery" target="_blank"><i class="far fa-question-circle"></i></a></label>
          <vs-input class="w-full" v-model="bankFunction.iconClass"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('DESCRIPTION') }}</label>
          <vs-textarea class="w-full" v-model="bankFunction.description"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <vs-checkbox v-model="bankFunction.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyBankFunction('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyBankFunction('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyBankFunction('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import BankFunctionService from '@/services/bank/BankFunctionService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'bank-function-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      bankFunction: {},
      typeBehaviours: []
    }
  },
  methods: {
    async load () {
      let self = this
      BankFunctionService.bankFunctions({
        functionId: this.$route.params.id
      }).then(function (response) {
        self.bankFunction = response.data.functionList[0] || {}
        self.bankFunction.disabled = self.bankFunction.disabled === 'Y'
      })

      EnumerationService.enumerations({
        enumTypeId: 'BankFunctionBehaviour',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.typeBehaviours = response.data.enumerationList || []
      })
    },
    modifyBankFunction (nextAction) {
      let self = this
      BankFunctionService.modifyBankFunction({
        functionId: this.$route.params.id,
        functionName: this.bankFunction.functionName,
        iconClass: this.bankFunction.iconClass,
        description: this.bankFunction.description,
        disabled: this.bankFunction.disabled ? 'Y' : 'N'
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewBankFunction', params: { id: response.data.functionId } })
          } else {
            self.$router.push({ name: 'BrowseBankFunctions' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
